@import url('https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');
:root {
  --body-bg: #f5f8ff;
  --box-bg: #fff;
  --input-bg: #f5f8ff;
  --txt-color: #2f2d2f;
  --txt-second-color: #ccc;
  --border-color: #00ccff;
  --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:10px 100px;
}

.nav-button{
  background-color: #00ccff;
  padding:10px 20px;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.auth-div{
    width:100vw;
    height:100vh;
    overflow:hidden;
    display: flex;
}

.auth-left-side{
    width:50%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}


.auth-back{
    position: absolute;
    top: 0;
    left: 0;
    z-index: -11;
    width:50vw;
    height: 100vh;
}

.left-title-div{
    margin: auto;
    width:450px;
    height:450px;
    background-color: rgba(255, 255, 255, 0.226);
    font-size: 80px;
    color:white;
    font-weight: 600;
    padding:20px;
    font-family: 'Zilla slab';
    border-radius: 7px;
}
.login-form-div{
    width: 50%;
    height: 100%;
}

.login-form{
    width:70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
    gap:20px;
}
.login-form h1{
    font-size: 40px;
}

.red{
    color:red;
    font-size: 10px;
}

.auth-inputs{
    margin:20px auto;
    overflow: hidden;
}



.auth-submit{
    border:none;
    outline:none;
    background-color: #00d8e7;
    padding:20px 40px;
    border-radius: 5px;
    font-size: 20px;
    color:white; 
    cursor: pointer;
}

.auth-submit:hover{
    background-color:#04b4c0;
}


.input-error{
    border: 0.5px solid red;
    border-radius: 5px;
  }
  
  
  .red{
    color:red;
    font-size:14px;
  }
  
  .auth-inputs{
    display: flex;
    flex-direction: column;
    margin:10px auto;
    justify-self: start;
    width: 100%;
    margin:10px auto;
    /* gap: 10px; */
  }
  .auth-password-input{
    width: 100%;
    display: flex;
    background-color: white;
    align-items: center;
  }
  
  .auth-inputs input{
    width:100%;
    padding: 20px;
    outline: none;
    margin:auto;    
    border: none;
    border-radius: 2px;
    font-size: 18px;
    background-color: rgb(247, 248, 248);
  }
  .auth-inputs label{
    margin:5px 0px;
  }
  
  .auth-inputs button{
    background:none;
    outline:none;
    border:none;
    position: absolute;
      cursor: pointer;
    right:130px;
  }

  .auth-inputs button svg{
    font-size: 22px;
  }

  .box {
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-items: center;
    background-color: var(--box-bg);
    padding: 30px;
    border-radius: 20px;
}

.header {
    margin-bottom: 30px;
    text-align: center;
}

.submit-button{
  padding: 20px 40px;
  background-color: #00ccff;
  border-radius: 5px;
  outline: none;
  border: none;
  color:white;
  font-size: 20px;
  margin: 10px auto;
  cursor: pointer;
}

.submit-button:hover{
  background-color:#05dbeb;
}


table{
  width:40%;
  text-align: center;
}

td, th{
  padding:10px 5px;
}

tr:nth-child(odd){
  background-color: rgb(214, 214, 214);
}



